.ul {
  padding-left: 20px;
  font-size: 16px;
  font-weight: 500;
  justify-content: space-between;
  width: 200px;
  list-style: none;
  border-radius: 5px;
}
.deleteContact {
  border: none;
  height: 20px;
  margin-left: 10px;
  border-radius: 4px;
  cursor: pointer;
}

.deleteContact:hover {
  background-color: rgb(255, 0, 0);
}

.textContent {
  justify-content: space-between;
  display: flex;
  margin-bottom: 5px;
}
